<template>
	<div class="body">
		<div class="report_nav flex_start">
			<div v-for="(item,index) of navList" :key="index" class="report_nav_list">
				<router-link :to="'/home/fundManageHome/reportSummaryHome/subFundTotal/'+item.router">
					{{item.name}}
				</router-link>
			</div>
		</div>
		<div class="main">
			<router-view></router-view>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'subFundTotal',
		data() {
			return {
				navList: [ 
					{
						name: '子基金投资组合总表',
						step: 1,
						router: 'subFundInvestTotal',
					}, 
					{
						name: '子基金认购基金组合总表',
						step: 2,
						router: 'subFundSubscriptTotal',
					}
				],
			}
		},
		created() {

		},
		mounted() {},
		components: {},
		methods: {},
	}
</script>

<style scoped="scoped">
	.body {
		padding: 0 30px 30px 30px;
		font-size: 14px;
	}
	
</style>


